<template>
  <div>
    <!-- 季度精选 导航-->
    <van-row type="flex" justify="center" class="biaoshi">
      <van-col span="7">
        <van-icon name="diamond-o" />
        <span class="title-biao">季度精选</span>
      </van-col>
      <van-col span="15"></van-col>
      <van-col span="1">
        <van-icon name="arrow" />
      </van-col>
    </van-row>

    <!-- 详情 -->
    <div class="sift-box">
      <div class="sift-item" v-for="(item,index) in productData" :key="index">
        <van-row gutter="20">
          <van-col span="12">
            <van-image fit="cover" height="157.86" lazy-load :src="item.smallImg" />
          </van-col>
          <van-col span="12">
            <h2>{{item.name}}</h2>
            <p class="van-multi-ellipsis--l3">{{item.desc}}</p>
            <van-button @click="detailShow(item.pid)" class="active" plain custom-style>查看详情</van-button>
          </van-col>
        </van-row>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      // 商品数据
      productData: []
    };
  },

  created() {
    // 获取商品详情数据
    this.getProductData();
  },

  computed: {
    ...mapState(["productDataCache"])
  },

  methods: {
    ...mapMutations(["changeData"]),

    getProductData() {
      if (this.productDataCache.length > 0) {
        this.productData = this.productDataCache;
        return;
      }

      // 加载事件
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/typeProducts",
        params: {
          appkey: this.appkey,
          key: "type",
          value: "coffee"
        }
      })
        .then(res => {
          if (res.status == 200) {
            this.$toast.clear();
            this.productData = res.data.result;

            // 缓存
            this.changeData({
              key: "productDataCache",
              value: this.productData
            });
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },

    // 查看详情页面跳转
    detailShow(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    }
  }
};
</script>

<style lang="less" scoped>
// 标签
.biaoshi {
  padding-top: 5px;
  height: 25px;
  font-size: 16px;
  position: relative;
  color: #2c1907;

  .title-biao {
    line-height: 25px;
    position: absolute;
    left: 35px;
  }
  .van-icon {
    font-size: 22px;
    color: #2c1907;
    line-height: 30px;
  }
}
// 详情
.sift-box {
  padding: 5px;
  .sift-item {
    box-shadow: 0 0 6px #646161;
    height: 170px;
    margin: 5px;
    padding: 5px;
    color: #130101;
    text-align: center;

    .van-button {
      box-shadow: 0 0 6px #130101;
      border-radius: 10px;
      width: 100px;
    }

    p {
      text-align: left;
    }
  }
}

.active:active {
  color: #fff;
  background-color: #36200c;
  box-shadow: none;
  border: none;
}
</style>